import React, { Component } from 'react';
import { Slider } from 'element-react';
import './index.scss';
import Codemirror from 'react-codemirror';
import { connect } from 'react-redux';
import { fromJS, is } from 'immutable';

class Xslider extends Component {
	state = {
		value1: 0,
		value2: 50,
		value3: 36,
		value4: 48,
		value5: 42
	}

	componentDidMount() {
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}
	
	formatTooltip = (val) => {
		return val / 100
	}

	render() {
		const { defaultOptions} = this.props.configs

		return (
			<div id="x-slider">
				<h3>Slider 滑块</h3>
				<div className="mt-20 mb-20">
					<Slider value={this.state.value1} />
					<Slider value={this.state.value2} />
					<Slider value={this.state.value3} showTooltip={false} />
					<Slider value={this.state.value4} formatTooltip={this.formatTooltip} />
					<Slider value={this.state.value3} disabled={true} />
				</div>
				<Codemirror value={
					`<Slider value={this.state.value1} />
<Slider value={this.state.value2} />
<Slider value={this.state.value3} showTooltip={false} />
<Slider value={this.state.value4} formatTooltip={this.formatTooltip} />
<Slider value={this.state.value3} disabled={true} />`
				} options={defaultOptions} />

			</div>
		);
	}
}

export default connect(state => ({
	configs: state.configs
}), {

	})(Xslider)
